@import '../../sass/colors';

.productPage {
  font-size: 1rem;
  color: $black;

  /* thriller */
  .thriller {
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    text-transform: uppercase;
    margin: .7rem 0 1rem;
    font-size: .95rem;

    @media screen and (min-width: 576px) {
      font-size: 1rem;
    }
  }

  /* breadcrumb */
  .breadcrumbBar {
    margin-bottom: 1rem;
    display: flex;
  }
  
  /* back link */
  .breadcrumbBar__backLink {
    font-size: 1.2rem;
    font-weight: 500;
    color: inherit !important;
    text-decoration: none !important;
    margin-right: 1.5rem;
    display: flex;
    align-items: center;
  
    &:hover,
    &:focus {
      color: $primary-color !important;
    }
  }
  
  /* list */
  .breadcrumbList {
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
    align-self: flex-end;
  }
  
  .breadcrumbBar__item:not(:last-child) {
    padding-right: .4rem;
  
    &::after {
      content: '/';
      padding-left: .4rem;
    }
  }
  
  .breadcrumbBar__link {
    color: inherit !important;
    text-decoration: underline;
  
    &:hover,
    &:focus {
      color: $primary-color !important;
    }
  }

  /* gallery & info */
  .galleryAndInfo {
    display: flex;
    flex-direction: column;
    margin-bottom: 2.5rem;

    @media screen and (min-width: 1200px) {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  /* related products */
  .relatedProducts {
    margin-bottom: 8rem;
  }

  .relatedProducts__title {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1.5rem;
  }
}
